@import "../variables";

.hero {
  gap: 5vw;
  justify-content: space-between;

  @media (orientation: "landscape") {
    height: 100vh;
    flex-direction: row;
    padding-right: 6vw;
    align-items: flex-end;
  }
}

.hero__imgWrapper {
  height: 100vh;
  width: 100%;
  flex: 1;
  // overflow: hidden;
  & img {
    object-fit: cover;
    height: 100%;
    width: 100%;
    object-position: top;
  }

  @media (orientation: "landscape") {
    width: 40vw;
    flex: none;
    & img {
      object-fit: cover;
    }
  }
}

.hero__contentWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-items: flex-start;
  width: 90%;
  gap: 32px;
  // margin-bottom: 28px;

  @media (orientation: "landscape") {
    display: flex;
    flex-direction: column;
    gap: 20vh;
    margin-bottom: 0;
  }
}

.hero__nameWrapper {
  font-style: normal;
  line-height: normal;
  color: $mainFontColor;

  & h1 {
    // font-size: 48px;
    font-weight: 700;
    font-size: clamp(40px, 7vw, 96px);
    margin-bottom: 12px;
  }

  & p {
    font-weight: 300;
    font-size: clamp(20px, 3vw, 36px);
  }

  // @media (min-width: $desktop) {
  //   font-size: 96px;
  // }
}

.hero__sossialLinks {
  display: flex;
  justify-content: space-between;
  gap: 16px;
  // width: 100%;

  // max-width: 300px;

  & li {
    width: fit-content;
    height: fit-content;

    & a {
      width: clamp(45px, 5vw, 60px);
      height: clamp(45px, 5vw, 60px);
      position: relative;
    }

    & img {
      // width: clamp(45px, 5vw, 60px);
      // height: clamp(45px, 5vw, 60px);
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  @media (orientation: landscape) {
    margin-right: 0;
    margin-left: auto;
    gap: 20px;
    // max-width: 300px;
  }
}

.hero__btn {
  background-color: $btnColor;
}
